	background: linear-gradient(to bottom,rgba(42, 42, 108, 1),rgba(71, 60, 146, 0.73)),url('/fondo1.jpeg');
	/* background-image: linear-gradient(to bottom, rgba(245, 246, 252, 0.52), rgba(117, 19, 93, 0.73)), url('/fondo1.jpeg'); */
